
.siteBar{
    width: 270px;
    height: 100%;
    background-color:#ffffff;
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    transition: all 0.3s ease 0s;
    

}

.burger{

   background-color: #5854ef;
  position: relative;
   display: block;
   margin: auto 0 auto 20px ;
  width: 30px;
  height: 20px;
}
.burger::before, 
   .burger::after{
       content: '';
       background-color: #fff;
       position: absolute;
       width: 100%;
       height: 2px;
       left: 0;
       transition: all 0.3s ease 0s;
}

.burger span {
  background-color: #fff;
  height: 2px;
  position: absolute;
  left: 0;
  bottom: 9px;
  width: 100%;
  transition: all 0.3s ease 0s;
  
}
.burger::before{
  top: 0;
}

.burger::after{
  bottom: 0;
}
   





@media screen and (max-width: 1000px) {
    .siteBar{
      width: 240px;
    }
}


.list{
    font-size: 25px;
    margin: 0 0 0 10px;

}
.item{
    padding: 10px;
    margin: 20px 0 20px 0;
}
.item a{
    font-size: 24px;
    color: #8793a3;

    display: flex;

    align-items: center;
    padding: 10px 0 10px 32px;
}
.item .activeLink{
    text-shadow: black;
    color: #e5c314;
}
.item:hover a{
    color: #5edfff;
}

.none{
    display: none;
}
.logo{
    cursor: pointer;
   
    margin: auto 5px auto 0 ;
}
.LBWrapper{
   background-color: #615dfa;
   width: 100%;
   display: flex;
  justify-content: space-between;
  height: 90px;

}



