.newPost__form{
    display: flex;
    flex-direction: column;

}

.userPosts{
    padding:20px;
    font-size: 30px;
    color: #1c1a1a;
    font-weight: 600;
}
.newPost__form button{
    padding: 10px 40px 10px 10px;
    background-color: #0CB658;
    align-self: flex-end;
    margin: 0 20px 10px 0;

}






.newPost__form textarea{
    display: block;
    max-width: 100%;
    height: 80px;
    resize: none;
    margin: 27px 10px 22px 0;
    font-size: 26px;
    padding: 0 0 0 10px;

}