.header{
   
    
   background-color: #5854ef;
    display: grid;
    grid-template-rows:1fr;
    grid-template-columns:90px 3fr 2fr 2fr;
    height: 90px;
}




.iconsNawBarWrap{
    margin: auto 0;

}
.iconsNawBar{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.icon{
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.logo{
    margin: auto 0 auto 10px;
    cursor: pointer;
}


.loginName{
    font-size: 24px;
    font-weight: 700;
    color: #ffffff;
    text-align: center;
    margin: auto 0 auto auto ;
}
.loginName:hover .logout{
    opacity: 1;
}

.logout{

    opacity: 0;
}

.burger{

   background-color: #615dfa;
   position: relative;
    display: block;
    margin: auto 0 auto 20px ;
   width: 30px;
   height: 20px;
}
.burger::before, 
    .burger::after{
        content: '';
        background-color: #fff;
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        transition: all 0.3s ease 0s;
}

.burger span {
   background-color: #fff;
   height: 2px;
   position: absolute;
   left: 0;
   bottom: 9px;
   width: 100%;
   transition: all 0.3s ease 0s;
   
}
.burger::before{
   top: 0;
}

.burger::after{
   bottom: 0;
}
    


