.post{
    display: flex;
    width: 100%;
    align-items: center;
    background-color: antiquewhite;
    padding: 15px;
    margin: 10px 0 ;


}
.post img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: block;

}

.text {
    font-size: 26px;
    margin-left: 35px;
}
.like{

    align-self: center;
    margin: 0 0 0 10px;

}

.like button{
    background-color: indigo;
    align-self: center;
    padding: 7px;
    border-radius: 40%;
}

.likeCounter{
    font-size: 14px;
    text-align: center;
    margin: 0 0 0 5px;
}